
body {
  font-family: Roboto, sans-serif;
}
.login {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100vw;
  height: 100vh;
  background-image: url(/static/img/background.d870041.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}
.login-form {
  padding: 10px 20px;
  -webkit-box-shadow: 0 0 2px 2px rgba(65, 65, 65, 0.3);
          box-shadow: 0 0 2px 2px rgba(65, 65, 65, 0.3);
  background: -webkit-gradient(linear, left top, left bottom, from(#6799ea), to(#abb9d6));
  background: linear-gradient(to bottom, #6799ea, #abb9d6);
  font-size: 18px;
  border-radius: 10px;
}
.login-form > div {
    padding: 4px 0;
}
.login-form__label {
    display: inline-block;
    width: 170px;
    padding-right: 10px;
    text-align: right;
    vertical-align: middle;
}
.login-form__input {
    width: 150px;
    height: 25px;
    line-height: 18;
    padding: 0 10px;
    outline: 0;
    border: 0;
    color: white;
    border: 1px solid #939496;
    vertical-align: middle;
    color: #272623;
    border-radius: 5px;
    background-color: #fff;
}
.login-form__button-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.login-form__button-container__btn {
      padding: 3px;
}

main {
  background-color: #d7e2f4;
}
.header {
  background: -webkit-gradient(linear, left top, left bottom, from(#1b62d6), to(#abb9d6));
  background: linear-gradient(to bottom, #1b62d6, #abb9d6);
  padding: 20px 15px;
  color: #fff;
}
.header__login {
    text-align: right;
    font-size: 20px;
}
.header__login > button {
      font-size: 60%;
      font-weight: 700;
      cursor: pointer;
      color: #d1d1d9;
      text-decoration: none;
      text-shadow: 0 -1px 2px rgba(0, 0, 0, 0.2);
      padding: 0.5em 1em;
      outline: none;
      border-radius: 3px;
      background: -webkit-gradient(linear, left top, left bottom, from(#6e7078), to(#515156)) #6e7078;
      background: linear-gradient(#6e7078, #515156) #6e7078;
      -webkit-box-shadow: 0 1px rgba(255, 255, 255, 0.2) inset, 0 3px 5px rgba(0, 1, 6, 0.5), 0 0 1px 1px rgba(0, 1, 6, 0.2);
              box-shadow: 0 1px rgba(255, 255, 255, 0.2) inset, 0 3px 5px rgba(0, 1, 6, 0.5), 0 0 1px 1px rgba(0, 1, 6, 0.2);
      -webkit-transition: 0.2s ease-in-out;
      transition: 0.2s ease-in-out;
}
.header__login > button:hover:not(:active) {
        background: -webkit-gradient(linear, left top, left bottom, from(#7e7e86), to(#46474c)) #7e7e86;
        background: linear-gradient(#7e7e86, #46474c) #7e7e86;
}
.header__search {
    text-align: center;
}
.header__search > input {
      width: 320px;
      height: 40px;
      font-size: 25px;
      border-radius: 10px;
      padding-left: 40px;
}
.header__search > button {
      color: white;
      font-size: 85%;
      font-weight: bold;
      text-decoration: none;
      text-shadow: -1px -1px #000;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
      padding: 0.8em 2em;
      outline: none;
      background-color: #000;
      background-image: linear-gradient(45deg, rgba(255, 255, 255, 0) 30%, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0) 70%), radial-gradient(190% 100% at 50% 0%, rgba(255, 255, 255, 0.7) 0%, rgba(255, 255, 255, 0.5) 50%, rgba(0, 0, 0, 0) 50%);
      background-repeat: no-repeat;
      background-size: 200% 100%, auto;
      background-position: 200% 0, 0 0;
      border-radius: 10px;
      -webkit-box-shadow: rgba(0, 0, 0, 0.3) 0 2px 5px;
              box-shadow: rgba(0, 0, 0, 0.3) 0 2px 5px;
}
.header__search > button:hover {
        -webkit-transition: 0.5s linear;
        transition: 0.5s linear;
        background-position: -200% 0, 0 0;
}
.header__main {
    text-align: center;
    padding-top: 20px;
}
.header__main > button {
      height: 95px;
      border-radius: 50%;
      cursor: pointer;
      font-size: 100%;
      font-weight: 700;
      color: #fff;
      text-shadow: #053852 -1px 1px, #053852 1px 1px, #053852 1px -1px, #053852 -1px -1px;
      text-decoration: none;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
      padding: 0.3em 0.7em;
      outline: none;
      background: #053852 repeating-linear-gradient(135deg, #053852, #053852 10px, #1679ad 10px, #1679ad 20px, #053852 20px);
      -webkit-box-shadow: inset -2px -2px rgba(0, 0, 0, 0.3), inset 2px 2px rgba(255, 255, 255, 0.3);
              box-shadow: inset -2px -2px rgba(0, 0, 0, 0.3), inset 2px 2px rgba(255, 255, 255, 0.3);
      -webkit-transition: background-position 999999s, color 999999s, text-shadow 999999s;
      transition: background-position 999999s, color 999999s, text-shadow 999999s;
}
.header__main > button:hover {
        opacity: 0.6;
}
.product {
  z-index: 1;
}
.product-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    max-width: 1280px;
}
.product-list__item {
      text-align: center;
      width: 300px;
      cursor: pointer;
      margin: 20px;
      -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
              box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
      border-radius: 5px;
      padding: 15px 5px;
      background-color: #fff;
}
.product-list__item:hover {
        -webkit-transform: scale(1.15);
                transform: scale(1.15);
        z-index: 10;
}
.product-list__buttons {
      text-align: center;
      padding: 30px 0 80px;
}
.product-list__buttons > button {
        color: white;
        text-decoration: none;
        padding: 0.5em 2em;
        outline: none;
        border-width: 2px 0;
        border-style: solid none;
        border-color: #FDBE33 #000 #D77206;
        border-radius: 6px;
        background: -webkit-gradient(linear, left top, left bottom, from(#F3AE0F), to(#E38916)) #E38916;
        background: linear-gradient(#F3AE0F, #E38916) #E38916;
        -webkit-transition: 0.2s;
        transition: 0.2s;
        height: 45px;
        font-size: 120%;
}
.product-list__buttons > button:hover {
          background: -webkit-gradient(linear, left top, left bottom, from(#f5ae00), to(#f59500)) #f5ae00;
          background: linear-gradient(#f5ae00, #f59500) #f5ae00;
}
.product-list__popup {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
@media only screen and (max-width: 768px) {
.product-list__popup {
          display: block;
}
}
.product-list__popup > p {
        padding: 20px 15px;
        font-size: 20px;
}
@media only screen and (max-width: 420px) {
.product-list__popup > img {
          width: 85vw;
}
}
.popup-title {
  font-size: 1.6em;
  font-weight: 800;
}
.firstSearch {
  text-align: center;
  padding: 30px 0 60px;
  cursor: pointer;
}
.firstSearch-active {
  text-align: center;
  padding: 30px 0 60px;
  cursor: pointer;
  height: 170vh;
}
.title {
  font-size: 1.2em;
  font-weight: 600;
}
